  componentDidMount() {
    const { BASE_URL } = constantes;
    superagent
      .get(`${BASE_URL}/vehiculos`)
      .then((vehiculos) => {
        this.setState({
          vehiculos: vehiculos.body.map((v) => ({ label: v.nombre, id: v.id })),
          vehiculosInited: true,
        });
      })
      .catch((err) => {
        this.setState({ vehiculos: [], vehiculosInited: true });
      });
    superagent
      .get(`${BASE_URL}/clientes`)
      .then((clientes) => {
        this.setState({
          clientes: clientes.body.map((c) => ({ label: c.nombre, telefono: c.telefono })),
          clientesInited: true,
        });
      })
      .catch((err) => {
        this.setState({ clientes: [], clientesInited: true });
      });
  }

  handleSuggestionsFetchRequestedVehiculos = ({ value }) => {
    this.setState({
      suggestionsVehiculos: getSuggestions(value, this.state.vehiculos),
    });
  };

  handleSuggestionsClearRequestedVehiculos = () => {
    this.setState({
      suggestionsVehiculos: [],
    });
  };

  handleSuggestionsFetchRequestedClientes = ({ value }) => {
    this.setState({
      suggestionsClientes: getSuggestions(value, this.state.clientes),
    });
  };

  handleSuggestionsClearRequestedClientes = () => {
    this.setState({
      suggestionsClientes: [],
    });
  };

  handleChange = name => (event, { newValue }) => {
    this.setState({
      [name]: newValue,
    });
  };

  handleOnFormChange = ({ target }) => {
    this.setState({ [target.name]: target.value });
  }

  handleChangeChecks = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  handleTableChange = (value, field, index, table) => {
    const { frenos, suspensiones } = this.state;
    const selectedTable = table === 'frenos' ? frenos : suspensiones;
    selectedTable[index][field] = value;
    if (table === 'frenos') {
      this.setState({ frenos: selectedTable });
    } else {
      this.setState({ suspensiones: selectedTable });
    }
  }

  handleChangeState = (field, value) => {
    this.setState({ [field]: value });
  }

  getNumberValue = (number) => {
    if (isNaN(number)) return 0;
    return Number(number);
  }

  handleCreateNew = () => {
    const {
      vehiculo,
      cliente,
      folio,
      cambioAceiteChecked,
      afinacionChecked,
      telefono,
      placas,
      otros,
      frenos,
      suspensiones,
      factura,
      costoOtros,
    } = this.state;
    const { BASE_URL } = constantes;
    let manoObraTotal = 0;
    let precio = 0;

    const servicios = [...frenos, ...suspensiones];
    servicios.forEach((servi) => {
      if (servi.activado && !isNaN(servi.precio) && !isNaN(servi.manoObra) && !isNaN(servi.cantidad)) {
        manoObraTotal += this.getNumberValue(servi.manoObra);
        precio += (this.getNumberValue(servi.cantidad) * this.getNumberValue(servi.precio));
      }
    });
    const total = manoObraTotal + precio + this.getNumberValue(costoOtros);
    const body = {
      nombreCliente: cliente,
			telefono,
			nombreVehiculo: vehiculo,
			placa: placas,
			manoObraTotal,
			factura,
			folio,
			total,
			servicios: servicios.map((s) => {
        if (s.activado) {
          s.manoObra = this.getNumberValue(s.manoObra);
          s.cantidad = this.getNumberValue(s.cantidad);
          s.precio = this.getNumberValue(s.precio);
        }
        return s;
      }),
			otros,
			cambioAceite: cambioAceiteChecked,
			afinacion: afinacionChecked,
      costoOtros: this.getNumberValue(costoOtros),
    };
    this.setState({ loading: true });
    superagent
      .post(`${BASE_URL}/nueva-orden`)
      .send(body)
      .then((response) => {
        this.setState({ loading: false });
        toast.success('Orden creada correctamente', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
        });
        setTimeout(() => {
          window.location = '/index.html';
        }, 3000);
      })
      .catch((err) => {
        console.error(err);
        this.setState({ loading: false });
      });
  }

  fillInfoVehicles = () => {
    const { BASE_URL } = constantes;
    setTimeout(() => {
      const {
        vehiculos,
        vehiculo: vehiculoNombre,
      } = this.state;
      const vehiculoSelected = vehiculos.find((v) => v.label === vehiculoNombre);
      if (vehiculoSelected) {
        this.setState({ loading: true });
        superagent
          .get(`${BASE_URL}/servicios-auto/${vehiculoSelected.id}`)
          .then((servicios) => {
            const { frenos, suspensiones } = servicios.body;
            this.setState({
              frenos,
              suspensiones,
              loading: false,
            });
          })
          .catch((err) => {
            console.error(err);
            this.setState({ loading: false });
          });
      }
    }, 300);
  }

  fillInfoClientes = () => {
    setTimeout(() => {
      const {
        clientes,
        cliente: clienteNombre,
      } = this.state;
      const selectedClient = clientes.find((cliente) => cliente.label === clienteNombre);
      if (selectedClient) this.setState({ telefono: selectedClient.telefono });
    }, 300);
  }

  renderSuggestionVehiculos = (suggestion, { query, isHighlighted }) => {
    const matches = match(suggestion.label, query);
    const parts = parse(suggestion.label, matches);

    return (
      <MenuItem onClick={this.fillInfoVehicles} selected={isHighlighted} component="div">
        <div>
          {parts.map((part, index) =>
            part.highlight ? (
              <span key={String(index)} style={{ fontWeight: 500 }}>
                {part.text}
              </span>
            ) : (
              <strong key={String(index)} style={{ fontWeight: 300 }}>
                {part.text}
              </strong>
            ),
          )}
        </div>
      </MenuItem>
    );
  }

  renderSuggestionClientes = (suggestion, { query, isHighlighted }) => {
    const matches = match(suggestion.label, query);
    const parts = parse(suggestion.label, matches);

    return (
      <MenuItem onClick={this.fillInfoClientes} selected={isHighlighted} component="div">
        <div>
          {parts.map((part, index) =>
            part.highlight ? (
              <span key={String(index)} style={{ fontWeight: 500 }}>
                {part.text}
              </span>
            ) : (
              <strong key={String(index)} style={{ fontWeight: 300 }}>
                {part.text}
              </strong>
            ),
          )}
        </div>
      </MenuItem>
    );
  }

